import React from 'react'
import moment from "./../../node_modules/moment/min/moment-with-locales"

export default function Note() {
    return (
			<div className="note">
				<div className="note-content">
					<span className="note-content-subject">Title</span>
					<span className="note-content-text">
						Lorem ipsum dolor, sit amet consectetur adipisicing elit. Doloribus,
						quibusdam. Lorem ipsum dolor sit amet consectetur, adipisicing elit.
						Inventore officiis quae at quo distinctio aliquam aspernatur,
						delectus, earum atque doloremque reiciendis suscipit voluptates
						voluptate dolorum ut nulla, adipisci odio iure? Lorem, ipsum dolor
						sit amet consectetur adipisicing elit. Asperiores autem eaque
						exercitationem, optio ut nobis, debitis odio minus voluptates amet
						dignissimos vel ipsum temporibus a natus alias numquam odit libero.
					</span>
				</div>
				<div className="note-data">
					<span className="note-data-time">{moment().format("h:mm a")}</span>
					<span className="note-data-date">
						{moment().format("D MMMM YYYY")}
					</span>
				</div>
				<div className="note-footer">
					<i className="lab la-2x la-facebook-f facebook-icon"></i>
					<i className="las la-2x la-copy "></i>
				</div>
			</div>
		);
}
